 import { ReactNode } from 'react';
 import { Slot } from '@radix-ui/react-slot';
 import { clsx } from 'clsx';
 

export interface ButtonProps {
    children: ReactNode;
    asChild?: boolean;
}

export function Button({ children, asChild }: ButtonProps) {
    const Comp = asChild ? Slot : 'button';

    return (
        <Comp 
        className={clsx(
            'py-4 px-3 bg-cyan-cyan rounded font-semibold text-black text-sm w-full transition-colors hover:bg-cyan-200 focus: ring-2 ring-gray-700',
        )}
        >
            {children}
        </Comp>
    )
}